body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  height: 100vh;
  background-color: whitesmoke;
}
.content{
  height: 80%;
  width: 100%;
  top:0; 
  left: 0;
  background-color: whitesmoke;
  position: relative; 
  
}
.content .grid{
  float: left;
  height: 100%;
  position: relative; 
}
#col1{
  width: 30%;
}
#col2{
  width: 70%;
}
#bins{
  height: 75%;
  width: 250px;
  top : 25%;
  float:right;
  position: relative;
  overflow: auto;
}
#bin{
  height: 70px;
  width: 200px;
  margin: 10px;
  border-radius: 7px;
  border: 0px;
  background-color: white;
  padding: 20px 20px;
  text-align: center;
  box-shadow: 10px 10px 8px darkseagreen;
  transition: 1s;
}
#bin:hover{
  background-color: lightgreen;
}
#bin:focus{
  background: lightcoral;
  border-radius: 70px;
  outline: none;
}
#bin:active{
  background: crimson;
  border-radius: 70px;
  outline: none;
}
#containers{
  width: 90%;
  height: 95%;
  top : 5%;
  left :5%;
  display: inline-block;
  position: absolute;
  border-radius: 12px;
  box-shadow: 10px 10px 8px darkseagreen;
  background-color: white;
  overflow: auto;
}
.title{
  font-style: initial;
  font-size: 25px;
  font-family: fantasy;
  color: white;
  background-color: lightgreen;
  box-shadow: 8px 8px 8px darkseagreen;
  border-radius: 10px;
  width: fit-content;
  position: relative;
}
.piechart{
  display: inline-flex;
  position: relative;
}
.trashes{
  display: inline-flex;
  width: 100%;
  height: 100px;
}
.trash{
  width: 33.333%;
  margin: 10px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 10px 10px 5px darkseagreen;
  border: 0px;
  font-size: 45px;
  font-family: fantasy;
  color: lightcoral;
  transition: 1s;
}
.trash:hover{
  background: #20B2AA;
  border-radius: 70px;
  outline: none;
  width: 22%;
}
.trash:focus{
  outline: none;
}

/* --Scroll bar-- */
::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
::-webkit-scrollbar-thumb {
  background: lightgreen; 
}
::-webkit-scrollbar-thumb:hover {
  background:  #03fc03; 
}